.Contributor {
  &-Container {
    display: inline-block;
    text-align: center;
    padding: 20px;
    margin: 20px;
    background-color: rgba(255, 255, 255, 0.5);
    border-radius: 25px;
    & img {
      height: 200px;
      width: auto;
    }
  }

  &-Name {
    font-weight: bold;
  }
}
